
.header {
  background-size: cover;
  background-position: center;
  background-image: url(../images/header.jpg);

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    padding: #{$headerTopPadding} 0 80px;
    box-sizing: border-box;
  }
  
  &__title {
    font-weight: bold;
    font-size: 46px;
    line-height: 40px;
    color: #fff;
    letter-spacing: -1px;
  
    @include onDesktop {
      font-size: 80px;
      line-height: 80px;
      letter-spacing: 1.5px;
    }
  
    &::before {
      content: "";
      display: block;
      width: 40px;
      height: 2px;
      margin-bottom: 40px;
      background-color: #fff;
    }
  }
    
  &__call {
    position: relative;
    
    @include onDesktop {
      &::before {
        content: "+1 234 555-55-55";
        position: absolute;
        top: 50%;
        transform: translate(-25%, - 50%);
        display: block;
        width: max-content;
        padding-right: 20px;
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #fff;
        opacity: 0;
        pointer-events: none;
        transition: 0.35s;
        transition-timing-function: linear;
        right: -75px;
      }
  
      &:hover::before {
        right: -10px;
        opacity: 1;
        pointer-events: all;
      }
    }
  }
}
